import {confirmLogin} from '@/utils/util.js'
export default {
	data() {
		return {
			userActivityInfo: {
				username: "天堂屠夫",
				total_visits: 0.,
				userActivityNum: {
					num1: 0,
					num2: 0,
					num3: 0,
					num4: 0
				},
				avatar: "../../static/test/waterfull/1.jpg"
			},
			advertisements: [
				"../../static/test/waterfull/1.jpg", "../../static/test/waterfull/2.jpg","../../static/test/waterfull/3.jpg"
			]

		}
	},
	methods: {
		open() {
			this.$refs.popup.open()
		},
		navtoChangePerson() {
			let isLogin = this.checkLogin('../self/self', '2')
			console.log(isLogin)
			if (!isLogin) {
				isLogin = true
				confirmLogin('weixin')
				uni.showModal({
				title: '确认登录',
				content: '要查看个人信息，需要登陆哦~',
				success: function (res) {
						if (res.confirm) {
							confirmLogin('weixin')
							if(isLogin){
								uni.showToast({
									title:'登陆成功！！',
									duration:1000,
									icon:"success"
								})
							}
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				uni.navigateTo({
					url: "../changePerson/changePerson",
					"animationType": "fade-in",
					"animationDuration": 300
				})
			}

		},
		toDetail() {
			let isLogin = this.checkLogin('../self/self', '2')
			if (!isLogin) {
				uni.setStorageSync('storage_key', 'hello');
				
			}else{
				uni.navigateTo({
					url: '../personalDetail/personalDetail',
					"animationType": "fade-in",
					"animationDuration": 300
				
				})
			}

		}
	},
	async onLoad() {
		let res = await this.request('me/', {
			uid: 1
		})
	}
}
